import { request } from '@/util/api.js';
import { url } from '@/util/http.js';
export default {
	data() {
		return {
			billDetail: {}
		};
	},
	onLoad(option) {
		console.log(option);
		this.getBillDetail(option.id);
	},
	methods: {
		async getBillDetail(id) {
			const detail = await request({ url: url + '?op=getdetail&id=' + id });
			console.log(detail);
			if (detail.data.code == 0) {
				this.billDetail = detail.data.info[0];
			} else {
				uni.showToast({
					title: detail.data.msg,
					success() {
						uni.navigateBack({});
					}
				});
			}
		},
		deleteBill(e) {
			uni.showModal({
				title: '是否删除该订单？',
				async success() {
					const resp = await request({ url: url + '?op=deteleBill&id=' + e });
					console.log(resp);
					if (resp.data.code === 0) {
						uni.showToast({
							title: resp.data.msg,
							icon: 'success',
							success() {
								uni.navigateBack({});
							}
						});
					} else {
						uni.showToast({
							title: resp.data.msg
						});
					}
				}
			});
		}
	}
};
